export default function s3Loader({ src }: { src: string }) {
  // if (process.env.NODE_ENV === "development") return src

    if (src.startsWith('/')) {
        // return `https://d2hviru9vjr5w.cloudfront.net${src}`;
        return `https://d1ecf6wbus577w.cloudfront.net/public${src}`;
    }
    return src;
}