"use client";
import {  useState } from "react";
import { Menu, X, ChevronDown } from "lucide-react";
import { NavLink } from "./NavLink";
// import { useGlobalStore } from "@/lib/store";
import Link from "next/link";

import type { Theme } from "@/lib/store";

const Navbar = () => {
  // const themeParam = useSearchParams().get("theme") as Theme | null;
  const [isOpen, setIsOpen] = useState(false);
  const [expandedItem, setExpandedItem] = useState<string | null>(null);

  const menuItems = [
    { id: "home", label: "Home", href: "/" },
    {
      id: "products",
      label: "Our Products",
      href: "/products",
      children: [
        { id: "gp-oil", label: "Gut Pro Oil", href: "/products", theme: "teal" as Theme },
        { id: "ww-oil", label: "Weight Watchers Oil", href: "/products", theme: "orange" as Theme },
        { id: "gp-brown-rice", label: "Gut Pro Basmati Brown Rice", href: "/products/gp-brown-rice", theme: "teal" as Theme },
        { id: "ww-brown-rice", label: "Weight Watchers Brown Rice", href: "/products/ww-brown-rice", theme: "orange" as Theme },
      ],
    },
    { id: "faqs", label: "FAQs", href: "/faq" },
    { id: "blogs", label: "Blogs", href: "/blogs" },
    { id: "contact", label: "Contact us", href: "/contact" },
  ];

  // useEffect(() => {
  //   if (themeParam) {
  //     setTheme(themeParam);
  //   }
  // }, [themeParam, setTheme]);
  return (
    <>
      {/* Desktop Navbar */}
      <nav
        className={`hidden md:flex justify-between md:gap-3 lg:gap-5 bg-transparent items-start
        transition-transform duration-1000 ease-out
        max-w-[50vw] `}
      >
        {menuItems.map((item) => (
          <div key={item.id} className="relative group">
            <NavLink
              key={item.href}
              href={item.href}
              className="
                whitespace-nowrap
                text-center
                px-3 py-1.5
                rounded-full
                text-sm/4.5
                font-normal
                bg-black/30
                text-white
                backdrop-blur-[10px]
                hover:bg-black/50
                transition
                duration-300
                ease-in-out
                cursor-pointer
              "
            >
              {item.label}
            </NavLink>

            {item.children && (
              <ul className="hidden group-hover:block absolute top-[120%]  py-3  bg-black/30 backdrop-blur-[10px] rounded-lg overflow-hidden capitalize whitespace-nowrap">
                {item.children.map((child) => (
                  <Link
                    key={child.id}
                    href={child.href}
                    onClick={(e) => {
                      e.stopPropagation();
                      // setTheme(child.theme);
                    }}
                    className="block px-4 py-2 text-white hover:bg-white/10 transition cursor-pointer"
                  >
                    {child.label}
                  </Link>
                ))}
              </ul>
            )}
          </div>
        ))}
      </nav>

      {/* Mobile View */}
      <div className="md:hidden">
        {/* Mobile Hamburger Button */}
        <button
          onClick={() => setIsOpen(!isOpen)}
          className="p-3 rounded-sm bg-foreground/25 border-1 border-white text-white backdrop-blur-xs active:bg-black/30"
          aria-label="Toggle menu"
        >
          <Menu size={24} />
        </button>

        {/* Mobile Overlay Menu */}
        {isOpen && (
          <div className="absolute top-0 left-0 right-0 bg-black/30 backdrop-blur-[10px] z-40 ">
            {/* Close Button */}
            <div className="flex justify-end px-6 py-5">
              <button
                onClick={() => setIsOpen(false)}
                className="p-3 rounded-sm bg-foreground/25 border-1 border-white text-white backdrop-blur-xs active:bg-black/30"
                aria-label="Close menu"
              >
                <X size={24} />
              </button>
            </div>
            <div className="flex flex-col divide-y-1 divide-white/50 px-6 text-center">
              {menuItems.map((item) => (
                <div key={item.id} className="relative">
                  {item.children ? (
                    <>
                      <button
                        onClick={() =>
                          setExpandedItem(
                            expandedItem === item.href ? null : item.href
                          )
                        }
                        className="w-full  py-3 text-white text-lg transition-colors flex items-center justify-center gap-1"
                      >
                        {item.label}
                        <ChevronDown
                          size={20}
                          className={`transition-transform duration-300 ${
                            expandedItem === item.href ? "rotate-180" : ""
                          }`}
                        />
                      </button>
                      {expandedItem === item.href && (
                        <div className="flex flex-col bg-black/20 rounded-lg overflow-hidden capitalize mb-2 ">
                          {item.children.map((child) => (
                            <Link
                              key={child.id}
                              href={child.href}
                              onClick={(e) => {
                                e.stopPropagation();
                                // setTheme(child.theme);
                                setIsOpen(false);
                              }}
                              className="px-4 py-2 text-white hover:bg-white/10 transition cursor-pointer"
                            >
                              {child.label}
                            </Link>
                          ))}
                        </div>
                      )}
                    </>
                  ) : (
                    <div className="py-3" onClick={() => setIsOpen(false)}>
                      <NavLink
                        key={item.id}
                        href={item.href}
                        activeClassName="bg-black/30"
                        className="w-full text-left  px-6 py-2 rounded-sm text-white text-lg transition-colors "
                      >
                        {item.label}
                      </NavLink>
                    </div>
                  )}
                </div>
              ))}
            </div>
          </div>
        )}
      </div>
    </>
  );
};

export default Navbar;
